import React, { Component } from 'react';
import MoSelector from './moSelector';
import Toggle from './toggle';
import AnalogWriteSlider from './analogWriteSlider';
import AnalogReadButton from './analogReadButton'
import Dial from "./dial";
import Timeline from "./timeline";
import TimelineConfig from "./timelineConfig"
import { Row, Button, Icon } from "antd";
import "antd/dist/antd.css";
import './hq.less';

// const hiGray = "rgba(255, 255, 255, 0.65)"

class Hq extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeUnit: 1,
      capture: false,
      dialPos: 0,
      activePointVal: null,
      areTimelinesPlaying: false,
      areTimelinesEditing: false,
      saveTrigger: false,
      clearTrigger: false,
      colors: this.getColors(this.props.allUnits, 1)
    };
  }

  // getColors(allUnits, activeUnit) {
  //   let colors = allUnits.map((id, idx) => {
  //     const opacity = id === activeUnit ? 1 : 0.25;
  //     return "hsla(" + ((idx) * 56 + 0) % 360 + ", 100%, 70%, " + opacity + ")";
  //   });
  //   // console.log(colors);
  //   return colors;
  // }

  getColors = (allUnits, activeUnit, activeOpacity = 1, inactiveOpacity = 0.2) => {
    let colors = allUnits.map((id, idx) => {
      const opacity = id === activeUnit ? activeOpacity : inactiveOpacity;
      return "hsla(" + ((idx) * 56 + 0) % 360 + ", 100%, 70%, " + opacity + ")";
    });
    // console.log(colors);
    return colors;
  }

  componentDidUpdate(prevProps, prevState) {
    const { allUnits, screen } = this.props;
    const { activeUnit } = this.state;
    if (allUnits !== prevProps.allUnits || activeUnit !== prevState.activeUnit) {
      const colors = this.getColors(allUnits, activeUnit);
      this.setState({ colors: colors });
    }
    if (screen !== prevProps.screen) {
      // console.log("width = " + screen.width + ", height = " + screen.height);
    }
  }

  onCaptureClick() {
    const capture = !this.state.capture;
    this.setState({ capture: capture });
    if (capture) {
      // console.log("idle");
      fetch('/rotate/0/0/' + this.state.activeUnit + '/')
        .then(res => res.json()).then(data => {
          // console.log(data);
        });
    }
  }

  onPlayClick() {
    this.setState({ areTimelinesPlaying: !this.state.areTimelinesPlaying });
  }

  onEditClick() {
    this.setState({ areTimelinesEditing: !this.state.areTimelinesEditing });
  }

  render() {
    const { activeUnit, colors } = this.state;
    const { allUnits, screen } = this.props;
    const playState = this.state.areTimelinesPlaying;
    let playIcon = playState === false ? "play-circle" : "pause-circle";
    const editState = this.state.areTimelinesEditing;
    let editIcon = editState === false ? "edit" : "lock";
    const captureIcon = this.state.capture ? "vertical-align-top" : "vertical-align-bottom";
    const activeColor = colors[allUnits.indexOf(activeUnit)];
    let topRow;
    if (screen) {
      if (screen.height > 300) {
        topRow =
          <div className="HQtopContainer">
            {/* <Row type="flex" justify="space-around" align="bottom"> */}
            <div className="HQpinBox">
              <Row type="flex" justify="start" align="middle" >
                <div className="HQpinUnit">
                  p10 <Toggle unitId={activeUnit} pin={10} />
                </div>
              </Row>
              <Row type="flex" justify="start" align="middle" >
                <div className="HQpinUnit">
                  p11 <Toggle unitId={activeUnit} pin={11} />
                </div>
              </Row>
              <Row type="flex" justify="start" align="middle" >
                <div className="HQpinUnit">
                  p12 <Toggle unitId={activeUnit} pin={12} />
                </div>
              </Row>
              <Row type="flex" justify="start" align="middle" >
                <div className="HQpinUnit">
                  p13 <Toggle unitId={activeUnit} pin={13} />
                </div>
              </Row>
              <Row type="flex" justify="start" align="middle" >
                <div className="HQpinUnit">
                  a3 <AnalogReadButton unitId={activeUnit} pin={17} />
                </div>
              </Row>
              <Row type="flex" justify="start" align="middle" >
                <div className="HQpinUnit">
                  a4 <AnalogReadButton unitId={activeUnit} pin={18} />
                </div>
              </Row>
            </div>
            <div className="HQvSliderBox" >
              <div className="HQvSliderUnit">
                <AnalogWriteSlider unitId={activeUnit} pin={14} />
                <br />
                <span className="HQvSliderTitle">a0</span>
              </div>
              <div className="HQvSliderUnit">
                <AnalogWriteSlider unitId={activeUnit} pin={15} />
                <br />
                <span className="HQvSliderTitle">a1</span>
              </div>
              <div className="HQvSliderUnit">
                <AnalogWriteSlider unitId={activeUnit} pin={16} />
                <br />
                <span className="HQvSliderTitle">a2</span>
              </div>
            </div>
            <div className="HQdialBox">
              <Dial
                unitId={activeUnit}
                steps={this.props.steps}
                radius={50}
                speed={255}
                capture={this.state.capture}
                getDialPos={(dialPos) => this.setState({ dialPos: dialPos })}
                incomingVal={this.state.activePointVal}
              />
              <Button type="link"
                onClick={this.onCaptureClick.bind(this)}
                className="HQcaptureIconUnit"
              >
                <Icon type={captureIcon} className="HQcaptureButtonText" style={{ color: activeColor }} />
              </Button>
              <span className="HQpageTitle">MO:{activeUnit}</span>
            </div>
            {/* </Row> */}
          </div>
      }
    }
    return (
      <div className="HQcontainer">
        {topRow}
        <Row type="flex" justify="start" align="top">
          <div className="HQtimelineBox">
            <div className="HQtimelineButtonRow">
              <Row type="flex" justify="space-between" align="middle">
                <div className="HQmoSelector">
                  {/* <Button type="link"
                    // onClick={this.onEditClick.bind(this)}
                    className="HQtimelineButtonUnit"
                  >
                  <Icon type="sync" className="HQbuttonText" />
                  </Button> */}
                  <MoSelector
                    defaultUnit={activeUnit}
                    allUnits={allUnits}
                    getActiveUnit={(active) => this.setState({ activeUnit: active })}
                    getColors={this.getColors}
                  // style={{ display: inline }}
                  />
                </div>
                <div className="HQtimelineButtonBox">
                  <Button type="link"
                    onClick={this.onPlayClick.bind(this)}
                    className="HQtimelineButtonUnit"
                  >
                    <Icon type={playIcon} className="HQbuttonText" />
                  </Button>
                  <Button type="link"
                    onClick={this.onEditClick.bind(this)}
                    className="HQtimelineButtonUnit"
                  >
                    <Icon type={editIcon} className="HQbuttonText" />
                  </Button>
                  <Button type="link"
                    onClick={() => { this.TimelineConfig.showModal() }}
                    className="HQtimelineButtonUnit"
                  >
                    <Icon type="setting" className="HQbuttonText" />
                  </Button>
                  <TimelineConfig onRef={ref => (this.TimelineConfig = ref)}
                    saveTrigger={this.state.saveTrigger}
                    clearTrigger={this.state.clearTrigger}
                    getSaveTrigger={(trigger) => this.setState({ saveTrigger: trigger })}
                    getClearTrigger={(trigger) => this.setState({ clearTrigger: trigger })}
                  />
                </div>
              </Row>
            </div>
            <Row type="flex" justify="start" align="bottom">
              <Timeline
                activeUnit={activeUnit}
                allUnits={allUnits}
                steps={this.props.steps}
                capture={this.state.capture}
                getActivePointVal={(apVal) => this.setState({ activePointVal: apVal })}
                inPointVal={this.state.dialPos}
                makePlay={this.state.areTimelinesPlaying}
                makeEdit={this.state.areTimelinesEditing}
                makeClear={this.state.clearTrigger}
                makeSave={this.state.saveTrigger}
                colors={colors}
              />
            </Row>
          </div>
        </Row>
      </div>
    );
  };
};

export default Hq;
