import React, { Component } from 'react';
import './App.less';
import AppMenu from './components/menu';
import Hq from './components/hq';

class App extends Component {
  constructor() {
    super();
    this.state = {
      page: "hq",
      height: window.innerHeight, 
      width: window.innerWidth,
      steps: 4096,
    };
    this.updateDimensions = this.updateDimensions.bind(this);
  }

  componentDidMount() {
    window.addEventListener("resize", this.updateDimensions);
    this.updateDimensions();
  }

  updateDimensions() {
    this.setState({
      screen: {
        height: window.innerHeight, 
        width: window.innerWidth
      }
    });
  }

  render() {
    const {page, screen} = this.state;
    let content;
    if(page === "hq") {
      content =           
        <Hq className="APhq" unitId={2} allUnits={[1, 2, 3, 6, 8, 11, 14, 15, 19, 22, 24, 25]} screen={screen} steps={this.state.steps} />
    }
    else if(page === "timelines") {
      content = 
        <div className="APtimelineContainer">
        </div>
    }
    return (
      <div className="App">
        <div className="APmenu">
          <AppMenu getChoice={ (choice) => this.setState({ page: choice }) } />
        </div>
        <div className="APoffset"></div>
        {content}
      </div>
    );
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.updateDimensions);
  }  
}

export default App;