import React, { Component } from 'react';
import { Menu, Icon } from 'antd';
import "antd/dist/antd.css";
import classes from './menu.module.css';

const { SubMenu } = Menu;

class AppMenu extends Component {
  constructor() {
    super();  
    this.state = {
      current: 'hq',
    };
  }

  handleClick = e => {
    this.setState({
      current: e.key,
    });
    this.props.getChoice(e.key);
  };

  render() {
    return (
      <Menu onClick={this.handleClick} selectedKeys={[this.state.current]} mode="horizontal" theme="dark" className={classes.menu} >

        <SubMenu
          title={
            <span className="submenu-title-wrapper">
              <Icon type="setting" />
              Mo Controls
            </span>
          }
        >
          <Menu.ItemGroup>
            <Menu.Item key="hq">Unit HQ</Menu.Item>
            <Menu.Item key="live">Live Motion</Menu.Item>
            <Menu.Item key="custom">Custom Message</Menu.Item>
          </Menu.ItemGroup>
        </SubMenu>
        <Menu.Item key="website">
          <a href="http://www.robotstack.com" target="_blank" rel="noopener noreferrer">
            RobotStack
          </a>
        </Menu.Item>
      </Menu>
    );
  }
}

export default AppMenu;
